  background-color: rgba(221, 70, 70, 0.8);
  padding: 0;
  border: none;
  border-radius: 0.25rem;
  width: 1.4rem;
  height: 1.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: 0.15s ease;
}
.selected-media-item__remove:hover {
  background-color: #DD4646;
}
.selected-media-item__remove .icon {
  width: 1rem;
  height: 1rem;
  color: #fff;
}
.selected-media-item__document {
  background-color: #f5f5fc;
  display: flex;
  align-items: center;
  justify-self: center;
  justify-content: center;
  border-radius: 0.25rem;
}
.selected-media-item__document::after {
  content: "";
  display: block;
  padding-bottom: 100%;
}
.selected-media-item__document .icon {
  width: 2.5rem;
  height: 2.5rem;
  margin-bottom: 0.5rem;
  color: rgba(0, 0, 0, 0.1);
}

.modal-content.has-active-dropzone::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 5;
  background-color: rgba(19, 130, 249, 0.35);
  border: 5px dashed #1382f9;
  pointer-events: none;
}
.modal-content.has-active-dropzone::after {
  content: attr(data-dropzone-text);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.4rem;
  color: #fff;
  font-weight: 700;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  pointer-events: none;
}

.media-accordions > * + * {
