  align-items: stretch;
}
.activity__icon-helper {
  margin-right: 1rem;
  position: relative;
  z-index: 1;
}
.activity__icon-helper::before {
  content: "";
  width: 1px;
  background-color: #e9f3fe;
  position: absolute;
  top: 0;
  bottom: -2rem;
  left: calc(50% - 1px);
  z-index: -1;
}
.activity__icon {
  width: 2.5rem;
  height: 2.5rem;
  color: #1382f9;
  background-color: #e9f3fe;
