}
.notification-card__icon svg {
  width: 2rem;
  height: 2rem;
}
.notification-card__content {
  padding: 1.5rem 2rem;
  width: calc(100% - 4rem);
}
.notification-card__title {
  font-size: 1.2rem;
  color: #434651;
}
.notification-card__description > * {
