  align-items: center;
}
.app-logo img {
  height: 20px;
  width: auto;
}

@media (max-width: 991.98px) {
  .app-sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 1100;
    min-width: 15rem;
    max-width: 25rem;
    background: linear-gradient(45deg, #f5f5fc, #fff);
    box-shadow: 0 5px 25px 0 rgba(42, 51, 83, 0.1), 0 5px 10px rgba(0, 0, 0, 0.05);
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
  }
  .app-sidebar:not(.is-open) {
    visibility: hidden;
  }
}
.app-sidebar__inside {
  padding: 0 2.5rem;
  margin: 1.5rem 0;
  overflow-y: auto;
  min-height: 300px;
  height: calc(100vh - 5rem - 40px - 60px);
}
@media (min-width: 992px) {
  .app-sidebar__inside {
    height: calc(100vh - 5rem - 40px - 60px - 1.5rem - 1.5rem);
  }
}
.app-sidebar__title {
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #a8abb8;
  padding-top: 0.75rem;
  margin-bottom: 0.5rem;
}
.app-sidebar__footer {
