  left: -2.5rem;
  width: 4px;
  background-color: #1382f9;
}
.app-sidebar-menu-item.is-open .app-sidebar-submenu {
  display: block;
}
.app-sidebar-menu-item.is-open .app-sidebar-menu-link {
  text-decoration: none;
  color: #434651;
}
.app-sidebar-menu-item.is-open .app-sidebar-menu-link .app-sidebar-menu-link__icon {
  color: #1382f9;
}

.app-sidebar-menu-link {
  display: flex;
  align-items: center;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #434651;
  font-weight: 600;
  font-size: 0.9375rem;
}
.app-sidebar-menu-link:hover, .app-sidebar-menu-link:focus, .app-sidebar-menu-link.is-active {
