  border-radius: 4px;
}

.simplebar-scrollbar:before {
  position: absolute;
  content: "";
  background: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  left: 0px;
  right: 0px;
  opacity: 0;
  transition: opacity 0.2s linear;
}

*,
::before,
::after {
  box-sizing: inherit;
}

::selection {
  background: #1382f9;
  color: #fff;
}

a {
